<script>
export default {
    name: 'tab',
    props: {
        name: {
            required: true,
        },
        selected: {
            default: false
        }
    },
    data() {
        return {
            isActive: false
        }
    },
    mounted() {
        this.isActive = this.selected
    }
}
</script>

<template>
    <div v-show="isActive">
        <slot>
        </slot>
    </div>
</template>
